textarea {
  resize: none;
  width: 100%;
  margin-top: 4px;
  padding: 2px; }

input {
  width: 100%;
  padding: 2px;
  margin-top: 4px;
  height: 26px;
  border: 1px solid black;
  border-radius: 2px;

  &:focus {
    outline: 2px solid rgba(0, 0, 255, 0.407); } }

.invalid {
  outline: 2px solid red;
  border: none;
  box-shadow: 1px 1px 2px 0 red;

  &:focus {
    outline: 2px solid red; } }

.error-text {
  position: absolute;
  color: red;
  font-size: 12px;
  margin-top: 2px;
  line-height: 110%; }

.text-field__container {
  width: 100%;
  margin: 0 auto;
  margin-bottom: 25px; }

.input__container {
  position: relative; }

.required {
  margin: 0 2px;
  color: red; }
